@import '../../../base';

$heavy-weight: 700;

%standard-font {
  font-family: $font-open-sans;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.1px;
  text-align: center;
  color: white;
}

.background-overlay {
  position: relative;

  .circle-container {

    .img-back {
      left: 0;
      z-index: -10;
      position: absolute;
      top: 35%;
      height: 360px;
      width: 800px;
  
      @media screen and (min-width: 768px) {
        top: 84px;
        height: 360px;
        width: 100%;
      }
      
      @media screen and (min-width: 1440px) {
        top: 132px;
        height: 360px;
        width: 100%;
      }
    }

    .bg-circle {
      width: 882px;
      height: 433px;
      margin-left: -40px;
      object-fit: contain;
      mix-blend-mode: multiply;
      will-change: opacity;
      margin-top: -110px;
  
      @media screen and (min-width: 1240px) {
        width: 1200px;
        height: 634px;
        margin-top: -110px;
      }
    }
  
    .img-front {
      position: absolute;
      right: 20%;
      top: 50%;
      width: 60%;
      height: 70%;
      object-fit: contain;
  
      @media screen and (min-width: 768px) {
        right: 0;
        top: 25%;
        width: 50%;
        height: 70%;
      }
      @media screen and (min-width: 1440px) {
        right: 0;
        width: 70%;
        height: 70%;
      }
    }
  }

  .title-container {
    position: absolute;
    top: 3%;
    max-width: 80%;
    left: 10%;

    @media screen and (min-width: 768px) {
      top: 3%;
      max-width: 35%;
      left: 20vw;
    }

    @media screen and (min-width: 1200px) {
      top: 36%;
      max-width: 25%;
      left: 20vw;
    }

    .title {
      font-family: $font-after-night;
      display: inline;
      font-size: 20px;
      font-weight: $heavy-weight;
      color: #ffffff !important;

      @media screen and (min-width: 768px) {
        font-size: 34px;
      }
    }

    .desc {
      @extend %standard-font;
      font-size: $font-size-smallest;
      font-weight: $font-weight-normal;
      text-align: left;
      color: #ffffff !important;

      @media screen and (min-width: 768px) {
        font-size:$font-size-small;
      }

      @media screen and (min-width: 1440px) {
        font-size: 19.5px;
      }
    }
  }
}