@import '../../base';

%standard-font {
  font-family: $font-open-sans;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.1px;
  text-align: center;
  color: white;
}

$heavy-weight: 700;

.career {
  overflow-x: hidden;

  @media screen and (min-width: 820px) {
    overflow: initial;
  }

  .content {

    .content-section {
      margin-top: 108px;
      margin-bottom: 108px;

      @media screen and (min-width: 1440px) {
        margin-top: 48px;
      }
    }

    .content-row {
      justify-content: center;
      margin-bottom: 24px;

      .benefits-li {
        .desc {
          font-size: $font-size-small;
          @media screen and (min-width: 786px) {
            max-width: 90%;
          }
        }
      }
    }

    .title {
      @extend %standard-font;
      font-size: $font-size-big;
      font-weight: $font-weight-heavy;
      color: $color-title;

      @media screen and (min-width: 1440px) {
        font-size: $font-size-extra-big;
      }
    }

    .title-sec {
      @extend %standard-font;
      font-size: $font-size-big;
      font-weight: $font-weight-heavy;
      color: $color-title;
    }

    .title-desc {
      @extend %standard-font;
      font-size: $font-size-smallest;
      color: $color-body;

      @media screen and (min-width: 1440px) {
        font-size: $font-size-small;
      }
    }

    .benefits-wrapper {
      list-style-image: url('../../assets/images/Career/CareerListStyle.svg');
      font-size: $font-size-smallest;
      line-height: 2.15;
      color: $color-body;

      @media screen and(min-width:768px) {
        font-size: 19.5px;
      }

      ul {
        padding: 0 20px;
        @media screen and(min-width:768px) {
          padding: 0;
        }
      }
    }

    .jobsOpening-wrapper {
      font-size: 19.5px;
      line-height: 2.15;
      color: $color-body;

      .job-header {
        width: 80%;
        padding: 20px;
        display: inherit;
        height: 10%;

        @media screen and(min-width: 1000px){
          width: 20%;
          text-align: center;
        }

        .nav-tabs {
          border-bottom: none;
          cursor: pointer;
          margin: auto;
          display: block;

          .active {
            cursor: pointer;
            border-radius: 29px;
            border: solid 2px transparent;
            background-image: linear-gradient(#ffffff, #ffffff), linear-gradient(103deg, #00d7af, #46737a) !important;
            background-origin: border-box;
            background-clip: content-box, border-box;
            outline: none;
          }
          // change for 2 lined job header
          .nav-item:nth-child(1) {
            
            .nav-link {

              p {
                margin-top: 0px;
              }
            }
          }

          .nav-item {
            margin: auto;

            .nav-link {
              width: 196px;
              height: 84px;
              border-radius: 29px;
              background-image: linear-gradient(#ffffff, #ffffff);
              box-shadow: 0 20px 15px 0 rgba(70, 115, 122, 0.15);
              margin: 12px;
              padding: 0;
  
              p {
                text-align: center;
                color: $color-title;
                font-weight: $font-weight-bold;
                font-size: $font-size-regular;
                margin-top: 20px;
              }
            }
          }
        }
      }

      .job-title-wrapper {
        margin-left: 0;
        padding: 20px 40px;
        width: 100%;

        @media screen and(min-width: 1000px){
          margin-left: 30px;
          width: 100%;
        }

        .job-title {
          width: 100%;
          
          .collapse {
            margin-bottom: 40px;
          }
  
          .button-medium-color {
            display: flex;
            align-items: center;
            cursor: pointer;
            width: 100%;
            height: 96px;
            border-radius: 0;
            background-image: linear-gradient(#ffffff, #ffffff), linear-gradient(111deg, #00d7af, #46737a);
            border: solid 1px #c8e0e4;
  
            &:before {
              background-image: linear-gradient(#ffffff, #ffffff), linear-gradient(111deg, #00d7af, #46737a);
            }
    
            p {
              color: $color-title;
              font-size: $font-size-small;
              font-weight: $font-weight-bold;
              text-align: left;
              margin-left: 0;

              @media screen and(min-width: 768px){
                font-size: $font-size-regular;
              }
            }

            .arrow{
              transform-origin: 50%;
              transition: 0.2s ease-in-out;
              float: right;
              width: 18px;
              margin: 10px 0 !important;

              @media screen and(min-width: 768px){
                margin: 0;
                width: 35px;
              }
            }
            .turn{
              transform-origin: 50%;
              -ms-transform: rotate(180deg) !important;
              -webkit-transform: rotate(180deg) !important;
              transform: rotate(180deg) !important;
              transition: 0.2s ease-in;
              float: right;
              width: 18px;
              margin: 10px 0 !important;

              @media screen and(min-width: 768px){
                margin: 0;
                width: 35px;
              }
            }
          }
          .card {
            background-color: rgba(255, 255, 255, 0.5);
            border: solid 1px #c8e0e4;
            text-align: left;
            border-radius: 0;

            .title {
              text-align: left;

              .no-list {
                list-style-type: none;
                padding-left:0;

                li {
                  margin-bottom: 10px;
                }
              }

              p {
                color: $color-title;
                font-size: $font-size-small;
                font-weight: $font-weight-semibold;
                padding: 14px 20px;

                @media screen and(min-width: 768px){
                  font-size: $font-size-regular;
                }
              }

              ul {
                list-style-type: disc;
                list-style-image: none;
                padding-top: 10px;
  
                li {
                  padding: 0 20px;
                  font-size: $font-size-smallest;
                  line-height: normal;
                  font-weight: $font-weight-normal;
                  color: $color-body;
                  letter-spacing: -0.2px;

                  @media screen and(min-width: 768px){
                    font-size: $font-size-small;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}